<template>
  <div class="addGrades">
    <div class="score">
      <ul>
        <li>
          <h3>必填分数</h3>
          <p>
            <span>数学:</span>
            <input type="text" v-model="math">
          </p>
          <p>
            <span>语文:</span>
            <input type="text" v-model="chinese">
          </p>
          <p>
            <span>英语:</span>
            <input type="text" v-model="english">
          </p>
        </li>
        <li>
          <h3>
            选填分数
            <span>(请选填三门课程分数)</span>
          </h3>
          <p>
            <span>物理:</span>
            <input type="text" v-model="physics">
          </p>
          <p>
            <span>历史:</span>
            <input type="text" v-model="history">
          </p>
          <p>
            <span>地理:</span>
            <input type="text" v-model="geography">
          </p>
          <p>
            <span>化学:</span>
            <input type="text" v-model="chemistry">
          </p>
          <p>
            <span>技术:</span>
            <input type="text" v-model="skill">
          </p>
          <p>
            <span>政治:</span>
            <input type="text" v-model="political">
          </p>
          <p>
            <span>生物:</span>
            <input type="text" v-model="biology">
          </p>
        </li>
        <li>
          <h3>总分数</h3>
          <p>{{totalScore}}</p>
        </li>
      </ul>
    </div>
    <div class="affirm_btn">
      <router-link to="/mine/myResult">
        <div class="aff_btn" @click="enter_add()">确认添加</div>
      </router-link>
    </div>
    <div class="hint">
      <span class="iconfont warn">&#xe8eb;</span>
      <div class="warm_prompt">
        <p>温馨提示:高考季只能绑定一次</p>
        <p>高考成绩，谨慎填写哦!</p>
      </div>
    </div>
  </div>
</template>
<script>
import { addGrades } from "@/request/api";
export default {
  name: "addGrades",
  data() {
    return {
      math: null,
      chinese: null,
      english: null,
      physics: null,
      history: null,
      geography: null,
      chemistry: null,
      skill: null,
      political: null,
      biology: null,
      totalScore: null
    };
  },
  methods: {
    enter_add() {
      this.onLoad();
    },
    // 获取数据
    onLoad() {
      // 调用api接口，并且提供了两个参数
      addGrades({
        id: 2,
        collegeEntranceExamGrade: [
          {
            subject: '语文',
            score: this.chinese
          },
          {
            subject: '数学',
            score: this.math
          },
          {
            subject: '英语',
            score: this.english
          },
          {
            subject: '政治',
            score: this.political,
          },
          {
            subject: '历史',
            score: this.history
          },
          {
            subject: '地理',
            score: this.geography
          },
          {
            subject: '物理',
            score: this.physics
          },
          {
            subject: '化学',
            score: this.chemistry
          },
          {
            subject: '生物',
            score: this.biology
          },
          {
            subject: '技术',
            score: this.skill
          },
        ]
      }).then(res => {
        // 获取数据成功后的其他操作
        console.log(res);
      });
    }
  },
  computed: {
    grossScore() {
      this.totalScore = Number(this.chinese) + Number(this.math) + Number(this.english) + Number(this.political) + Number(this.physics) + Number(this.chemistry) + Number(this.history) + Number(this.biology ) + Number(this.skill) + Number(this.geography);
      return this.totalScore = String(this.totalScore);
    }
  }
};
</script>
<style scoped>
/*  */
.score {
  width: 100%;
  display: flex;
  justify-content: center;
}
.score li {
  width: 3.65rem;
  overflow: hidden;
  margin-top: 0.1rem;
}
.score li:first-child {
  width: 3.65rem;
  /* height: 1.2rem; */
}
.score li:first-child h3 {
  width: 100%;
  height: 0.4rem;
  background: #b2dfdb;
  color: #00796b;
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.score li p {
  background: #fff;
  border-bottom: 0.01rem solid lightgray;
}
.score li p span {
  font-size: 0.16rem;
  text-indent: 0.2rem;
  margin-left: 0.2rem;
  color: #333333;
}
.score li p input {
  width: 3.04rem;
  height: 0.4rem;
}
.score li:nth-child(2) h3 {
  width: 100%;
  height: 0.4rem;
  background: #bbdefb;
  line-height: 0.4rem;
  text-indent: 0.2rem;
  color: #1976d2;
}
.score li:nth-child(2) h3 span {
  font-size: 0.12rem;
}
.score li:last-child h3 {
  width: 100%;
  height: 0.4rem;
  background: #d1c4ea;
  line-height: 0.4rem;
  text-indent: 0.2rem;
  color: #7e57c2;
}
.score li:last-child p {
  width: 100%;
  height: 0.4rem;
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
/* 温馨提示样式 */
.warn {
  font-size: 0.18rem;
  color: #efca1e;
  margin-right: 0.1rem;
  margin-top: -0.03rem;
}
.hint {
  display: flex;
  justify-content: center;
  margin-top: 0.3rem;
  margin-bottom: 0.2rem;
}
.warm_prompt p {
  text-align: center;
  color: #9c9c9c;
}
/* 确认添加按钮 */
.aff_btn {
  width: 2.58rem;
  height: 0.5rem;
  text-align: center;
  background: #28a289;
  margin-top: 0.2rem;
  color: #fff;
  line-height: 0.5rem;
  border-radius: 0.05rem;
}
.affirm_btn {
  display: flex;
  justify-content: center;
  margin-top: 0.2rem;
}
</style>




