<template>
  <div class="city">
    <div class="input_score">
      <p @click="cho_city()">
        选择城市
        <i class="iconfont rotate">&#xe600;</i>
      </p>
    </div>
    <div class="school">
      <ul>
        <li>
          <div class="school_l">
            <div class="school_img"></div>
            <h3>北京大学</h3>
          </div>
          <div class="school_r">
            <p>学校简介：初名京师大学堂，是中国...</p>
            <p>地址：北京市海淀区颐和园路5号</p>
            <p>
              <span>985</span>
              <span>双一流</span>
              <span>公办</span>
            </p>
          </div>
        </li>
        <li>
          <div class="school_l">
            <div class="school_img"></div>
            <h3>清华大学</h3>
          </div>
          <div class="school_r">
            <p>学校简介：初名京师大学堂，是中国...</p>
            <p>地址：北京市海淀区颐和园路5号</p>
            <p>
              <span>985</span>
              <span>双一流</span>
              <span>公办</span>
            </p>
          </div>
        </li>
        <li>
          <div class="school_l">
            <div class="school_img"></div>
            <h3>浙江大学</h3>
          </div>
          <div class="school_r">
            <p>学校简介：初名京师大学堂，是中国...</p>
            <p>地址：北京市海淀区颐和园路5号</p>
            <p>
              <span>985</span>
              <span>双一流</span>
              <span>公办</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <levelThree ref='son'></levelThree>
  </div>
</template>
<script>
import levelThree from '../common/levelThree'
export default {
  components: {
    levelThree
  },
  methods: {
    cho_city() {
      this.$refs.son.showAddressPicker();
    }
  },
};
</script>
<style scoped>
/* 下箭头样式 */
.rotate {
  display: inline-block;
  font-size: 0.12rem;
  color: #FFB84C;
  transform:rotate(90deg);
}
/*  */
.input_score {
  width: 100%;
  height: 0.34rem;
  background: #fff;
  margin-bottom: 0.05rem;
}
.input_score p {
  font-size: 0.16rem;
  color: #333;
  line-height: 0.34rem;
  text-align: center;
}
/* 学校样式 */
.school ul li {
  width: 3.65rem;
  margin-left: 0.05rem;
  margin-bottom: 0.05rem;
  border-radius: 0.05rem;
  overflow: hidden;
  background: #fff;
  display: flex;
  justify-content: space-between;
}
.school_l {
  margin: 0.13rem 0.23rem 0.12rem 0.19rem;
}
.school_img {
  width: 0.77rem;
  height: 0.49rem;
  background: greenyellow;
}
.school_l h3 {
  font-size: 0.16rem;
  color: #373838;
  width: 0.77rem;
  text-align: center;
  margin-top: 0.12rem;
}
.school_r {
  width: 2.15rem;
  margin-top: 0.13rem;
  margin-right: 0.32rem;
}
.school_r p {
  font-size: 0.12rem;
  color: #333;
}
.school_r p:first-child {
  margin-bottom: 0.08rem;
}
.school_r p:nth-child(2) {
  margin-bottom: 0.21rem;
}
.school_r p:last-child span:first-child {
  display: inline-block;
  width: 0.3rem;
  text-align: center;
  line-height: 0.15rem;
  background: #28a289;
  color: #fff;
  margin-right: 0.21rem;
  border-radius: 0.03rem;
}
.school_r p:last-child span:nth-child(2) {
  display: inline-block;
  width: 0.4rem;
  margin-right: 0.21rem;
  text-align: center;
  line-height: 0.15rem;
  background: #28a289;
  color: #fff;
  border-radius: 0.03rem;
}
.school_r p:last-child span:last-child {
  display: inline-block;
  width: 0.3rem;
  text-align: center;
  line-height: 0.15rem;
  background: #28a289;
  color: #fff;
  border-radius: 0.03rem;
}
</style>



