<template>
  <div class="three-level-address" id="three_level_address">
    <div class="region-div">
      <!-- <button class="cho_btn" readonly="readonly" @click="showAddressPicker()">
        <span>选择城市</span>
        <i class="el-icon-caret-bottom"></i>
      </button>-->
      <mt-popup v-model="regionVisible" position="bottom" class="region-popup">
        <mt-picker
          :slots="myAddressSlots"
          valueKey="name"
          :visibleItemCount="3"
          @change="addressChange"
          :itemHeight="40"
          :showToolbar="true"
        >
          <div class="wrap">
            <button @click="handleConfirm" class="cancel">取消</button>
            <button @click="handleConfirm" class="ensure">确定</button>
          </div>
        </mt-picker>
      </mt-popup>
    </div>
  </div>
</template>

<script>
//引入省市区数据json文件
import threeLevelAddress from "../../../static/js/json/threeLevelAddress.json";

// 封装的省市区三级联动插件
export default {
  props: ["dc"],
  data() {
    return {
      region: "", //三级地址
      province: "", //省
      city: "", //市
      county: "", //县
      provinceCode: "", //省级代码
      cityCode: "", //市级代码
      countyCode: "", //县级代码
      regionVisible: false, //弹出框是否可见
      regionInit: false, //禁止地区选择器自动初始化，picker组件会默认进行初始化，导致一进入页面就会默认选中一个初始3级地址

      //picker组件插槽
      myAddressSlots: [
        //省
        {
          flex: 1,
          values: this.getProvinceArr(), //省份数组
          className: "slot1",
          textAlign: "center"
        }, //分隔符
        {
          divider: true,
          content: "",
          className: "slot2"
        }, //市
        {
          flex: 1,
          values: this.getCityArr("北京市"),
          className: "slot3",
          textAlign: "center"
        },
        {
          divider: true,
          content: "",
          className: "slot4"
        }, //县
        {
          flex: 1,
          values: this.getCountyArr("北京市", "北京市"),
          className: "slot5",
          textAlign: "center"
        }
      ]
    };
  },

  methods: {
    //打开地址选择器
    showAddressPicker() {
      this.regionVisible = true;
    },
    handleConfirm() {
      this.regionVisible = false;
    },
    //picker组件的change事件，进行取值赋值
    addressChange(picker, values) {
      console.log(picker);
      console.table(values);
      if (this.regionInit) {
        //取值并赋值
        this.region = values[0]["name"] + values[1]["name"] + values[2]["name"];
        this.province = values[0]["name"];
        this.city = values[1]["name"];
        this.county = values[2]["name"];
        this.provinceCode = values[0]["code"];
        this.cityCode = values[1]["code"];
        this.countyCode = values[2]["code"];
        console.log(picker.getSlotValue(0));
        console.table(picker.getSlotValues(0));
        console.table(picker.getValues()); //给市、县赋值
        picker.setSlotValues(1, this.getCityArr(values[0]["name"]));
        picker.setSlotValues(
          2,
          this.getCountyArr(values[0]["name"], values[1]["name"])
        );
      } else {
        this.regionInit = true;
      }
    }, //遍历json，返回省级对象数组
    getProvinceArr() {
      let provinceArr = [];
      threeLevelAddress.forEach(function(item) {
        let obj = {};
        obj.name = item.name;
        obj.code = item.code;
        provinceArr.push(obj);
      });
      return provinceArr;
    }, //遍历json，返回市级对象数组
    getCityArr(province) {
      // console.log("省：" + province);
      let cityArr = [];
      threeLevelAddress.forEach(function(item) {
        if (item.name === province) {
          item.children.forEach(function(args) {
            let obj = {};
            obj.name = args.name;
            obj.code = args.code;
            cityArr.push(obj);
          });
        }
      });
      return cityArr;
    }, //遍历json，返回县级对象数组
    getCountyArr(province, city) {
      let countyArr = [];
      threeLevelAddress.forEach(function(item) {
        if (item.name === province) {
          item.children.forEach(function(args) {
            if (args.name === city) {
              args.children.forEach(function(param) {
                let obj = {};
                obj.name = param.name;
                obj.code = param.code;
                countyArr.push(obj);
              });
            }
          });
        }
      });
      // console.log(countyArr);
      return countyArr;
    }
  },
  
};
</script>
<style scoped>
/*  */
.wrap {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 0.3rem;
}
.cancel {
  width: 0.5rem;
  height: 0.3rem;
  border: 0;
  background: #28a289;
  color: #fff;
  line-height: 0.2rem;
  outline: none;
}
.ensure {
  width: 0.5rem;
  height: 0.3rem;
  border: 0;
  background: #28a289;
  color: #fff;
  line-height: 0.2rem;
  outline: none;
}
/*  */
.cho_btn {
  width: 0.8rem;
  height: 0.24rem;
  background: #ffb84c;
  border: 0;
  display: flex;
  justify-content: space-around;
  border-radius: 0.03rem;
  outline: none;
  position: absolute;
  right: 0.1rem;
  top: 0.42rem;
}
.cho_btn span,
i {
  font-size: 0.12rem;
  color: #fff;
  line-height: 0.24rem;
}
.three-level-address {
  width: 100%;
  text-align: left;
  color: #ffffff;
}
.region-div {
  width: 100%;
}
.input-icon {
  display: inline-block;
  vertical-align: middle;
}
.input-icon i {
  font-size: 2rem;
}
.region-popup {
  width: 100%;
}
.data-show-div {
  margin-left: 1rem;
  color: #45c473;
}
.data-show-div span {
  color: #ffffff;
  font-size: 0.8rem;
}
</style>




