<template>
  <div class="myProfile">
    <div class="stu_msg">
      <ul>
        <li>
          <span>考生姓名:</span>
          <input type="text" v-model="studentName">
        </li>
        <li>
          <span>性别:</span>
          <input type="text" v-model="sex">
        </li>
        <li>
          <span>学校名称:</span>
          <input type="text" v-model="schoolName">
        </li>
        <li>
          <span>高考年份:</span>
          <input type="text" v-model="examYear">
        </li>
        <li>
          <span>就读城市:</span>
          <input type="text" v-model="liveCity">
        </li>
      </ul>
    </div>
    <div class="save_btn">
      <div class="save_mes" @click="saveMes()">保存</div>
    </div>
    <Popup v-if="isShow" title="保存成功"></Popup>
  </div>
</template>
<script>
import {studentMsg} from "@/request/api"
import Popup from "../common/Popup";
export default {
  name: "stu_msg",
  data() {
    return {
      isShow: false,
      studentName: null,
      sex: null,
      schoolName: null,
      examYear: null,
      liveCity: null,
    };
  },
  methods: {
    saveMes() {
      this.isShow = true;
      clearInterval(timer1);
      let timer1 = setTimeout(() => {
        //此时的this指向是该vue组件，不管在哪个地方使用，指向都是该vue组件。
        this.isShow = false;
      }, 1000);
      this.saveMsg();
    },
    saveMsg() {
      studentMsg({
          id: 2,
          studentName: this.studentName,
          gender: this.sex,
          highSchool: this.schoolName,
          examYear: this.examYear,
          address: this.liveCity

      }).then(res => {
        console.log(res);
      })
    }
  },
  components: {
    Popup
  }
};
</script>
<style scoped>

/* 保存按钮样式 */
.save_btn {
  width: 100%;
  margin-top: 1.64rem;
  display: flex;
  justify-content: center;
}
.save_mes {
  width: 2.58rem;
  height: 0.5rem;
  color: #ffffff;
  background: #28A289;
  line-height: 0.5rem;
  text-align: center;
  border-radius: 0.05rem;
}
.stu_msg {
  width: 100%;
  overflow: hidden;
  background: #ffffff;
}
.stu_msg li {
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
  border-bottom: 0.01rem solid #f3f3f3;
}
.stu_msg li span {
  font-size: 0.16rem;
  margin-left: 0.15rem;
}
.stu_msg li input {
  line-height: 0.4rem;
  font-size: 0.16rem;
}
</style>



