<template>
  <div class="test_topic">
    <div class="title_topic">
      <h3>测评答题</h3>
    </div>
    <div class="recommend">
      <p>为了更专业的推荐，小伙伴们</p>
      <p>赶快测一测吧!</p>
    </div>
    <div class="topic_box">
      <h4>第 1 题</h4>
      <p>通过兴趣问题，让你认识到自己真正的兴趣，帮助你找到理想的专业？</p>
      <div class="answer">
        <div class="yes">
          <span>是</span>
        </div>
        <div class="no">
          <span>否</span>
        </div>
      </div>
    </div>
    <div class="left_but">
      <canvas id="firCan" width="1000" height="500">您的浏览器版本过低, 不支持canvas, 请升级.</canvas>
    </div>
    <div class="right_but">
      <canvas id="sonCan" width="1000" height="500">您的浏览器版本过低, 不支持canvas, 请升级.</canvas>
    </div>
    <div class="submit">
      <span>提交</span>
    </div>
    <div :class="isShow ? 'pop_up' : 'pou_upA'">
        <p>欢迎进入测评系统</p>
        <p>此次测试题目为能力测试</p>
        <p>请认真答题, 以免导致结果不准确</p>
        <div class="res">
            <div class="wait" @click="go_out()">
                <span>稍后答题</span>
            </div>
            <div class="now" @click="begin()">
                <span>立即答题</span>
            </div>
        </div>
    </div>
    <div :class="isMask ? 'mask' : 'maskA'"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isMask: true,
      isShow: true
    }
  },
  methods: {
    go_out() {
      this.$router.push({name: 'exportsTest'});
    },
    begin() {
      this.isMask = false;
      this.isShow = false;
    }
  },
  mounted() {
    // 1,获取canvas
    var canvas = document.getElementById("firCan");
    // 2,获取2D渲染上下文
    var context = canvas.getContext("2d");
    // 3, 绘制圆
    context.beginPath();
    context.arc(-120, 130, 150, 0, Math.PI * 2, false);
    context.closePath();
    context.fillStyle = "#FE4266";
    context.fill();

    // 1,获取canvas
    var canvas = document.getElementById("sonCan");
    // 2,获取2D渲染上下文
    var context = canvas.getContext("2d");
    // 3, 绘制圆
    context.beginPath();
    context.arc(170, 130, 150, 0, Math.PI * 2, false);
    context.closePath();
    context.fillStyle = "#FE4266";
    context.fill();
  }
};
</script>
<style scoped>
/* 刚点进来的弹窗样式 */
.pop_up {
  width: 2.53rem;
  height: 2.15rem;
  background: #fff;
  border-radius: 0.05rem;
  position: absolute;
  top: 1.66rem;
  left: 0.6rem;
  z-index: 1;
}
.pop_upA {
  display: none;
}
.pop_up p {
  width: 100%;
  text-align: center;
  line-height: 0.2rem;
}
.pop_up p:first-child {
  padding-top: 0.3rem;
}
.pop_up .res {
  margin-top: 0.55rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.pop_up .res .wait {
  width: 0.75rem;
  height: 0.4rem;
  border: 0.015rem solid #FFBC3C;
  text-align: center;
  line-height: 0.4rem;
  color: #3A3A39;
  border-radius: 0.05rem;
}
.pop_up .res .now {
  width: 0.75rem;
  height: 0.4rem;
  background: #FFBC3C;
  text-align: center;
  line-height: 0.4rem;
  color: #fff;
  border-radius: 0.05rem;
}
/* 蒙版样式 */
.mask {
    width: 100%;
    background: #000;
    opacity: 0.6;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.maskA {
  display: none;
}
/* 父级div样式 */
.test_topic {
  position: relative;
  overflow: hidden;
}
/* 左右滑动样式 */
.left_but {
  width: 0.49rem;
  height: 2.56rem;
  border-radius: 0.03rem;
  background: #fff;
  position: absolute;
  left: 0;
  top: 2rem;
}
.right_but {
  width: 0.49rem;
  height: 2.56rem;
  border-radius: 0.03rem;
  background: #fff;
  position: absolute;
  right: 0;
  top: 2rem;
}
#firCan {
  position: absolute;
  left: 0;
  top: 0;
}
#sonCan {
  position: absolute;
  left: 0;
  top: 0;
}
/* 提交按钮样式 */
.submit {
  width: 2.58rem;
  height: 0.5rem;
  background: #28a289;
  text-align: center;
  line-height: 0.5rem;
  border-radius: 0.05rem;
  margin-top: 0.66rem;
  margin-left: 0.6rem;
  margin-bottom: 0.23rem;
  color: #fff;
}
/* 题目样式 */
.answer {
  width: 100%;
  margin-top: 0.61rem;
  display: flex;
  justify-content: space-between;
}
.answer .yes {
  width: 0.75rem;
  height: 0.4rem;
  border: 0.02rem solid #ffbc3c;
  border-radius: 0.03rem;
  text-align: center;
  line-height: 0.4rem;
  margin-left: 0.2rem;
  color: #616161;
}
.answer .no {
  width: 0.75rem;
  height: 0.4rem;
  border: 0.02rem solid #ffbc3c;
  border-radius: 0.03rem;
  text-align: center;
  line-height: 0.4rem;
  margin-right: 0.2rem;
  color: #616161;
}
.topic_box {
  width: 2.5rem;
  height: 2.7rem;
  background: #fff;
  border-radius: 0.03rem;
  margin-left: 0.63rem;
  margin-top: 0.37rem;
}
.topic_box h4 {
  font-size: 0.17rem;
  color: #616161;
  padding-top: 0.31rem;
  margin-bottom: 0.28rem;
  margin-left: 0.04rem;
}
.topic_box p {
  color: #616161;
  font-size: 0.13rem;
  line-height: 0.18rem;
  width: 2.2rem;
  height: 0.35rem;
  margin-left: 0.16rem;
}
/* 推荐样式 */
.recommend {
  width: 100%;
  margin-top: 0.3rem;
}
.recommend p {
  text-align: center;
  line-height: 0.2rem;
}
/* 标题样式 */
.title_topic {
  width: 100%;
  height: 0.4rem;
  background: #28a289;
}
.title_topic h3 {
  font-size: 0.18rem;
  color: #fff;
  text-align: center;
  line-height: 0.4rem;
}
</style>



