<template>
  <div class="academy">
    <div class="school">
      <ul>
        <li v-for="(value, index) in schools" :key="index">
          <div class="school_top">
            <h3>{{value.name}}</h3>
          </div>
          <div class="school_bot">
            <div class="school_l">
              <div class="school_img">
                <img :src="value.thumbnail" alt>
              </div>
            </div>
            <div class="school_r">
              <p>学校简介：{{value.briefIntr}}</p>
              <p>地址：{{value.addr}}</p>
              <p>
                <span v-for="(val, num) in value.level" :key="num">{{val}}</span>
              </p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { studentFoucs } from "@/request/api";
export default {
  data() {
    return {
      schools: null
    };
  },
  created() {
    this.getMajor();
  },
  methods: {
    getMajor() {
      studentFoucs({
        id: 2
      }).then(res => {
        console.log(res.data);
        this.schools = res.data.myCollege;
        console.log(this.schools);
      });
    }
  }
};
</script>
<style scoped>
.academy {
  margin-top: 0.11rem;
}
.school ul li {
  width: 3.65rem;
  margin-left: 0.05rem;
  margin-bottom: 0.05rem;
  border-radius: 0.05rem;
  overflow: hidden;
  background: #fff;
  display: flex;
  flex-wrap: wrap;
}
.school_bot {
  display: flex;
  justify-content: space-between;
}
.school_top {
  width: 100%;
}
.school_top h3 {
  text-align: center;
  line-height: 0.3rem;
}
.school_l {
  margin: 0.13rem 0.23rem 0.12rem 0.19rem;
}
.school_img {
  width: 0.77rem;
  height: 0.49rem;
}
.school_img img {
  width: 100%;
}
.school_l h3 {
  font-size: 0.16rem;
  color: #373838;
  width: 0.77rem;
  text-align: center;
  margin-top: 0.12rem;
}
.school_r {
  width: 2.15rem;
  margin-top: 0.13rem;
  margin-right: 0.32rem;
}
.school_r p {
  font-size: 0.12rem;
  color: #333;
}
.school_r p:first-child {
  margin-bottom: 0.08rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.school_r p:nth-child(2) {
  margin-bottom: 0.1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.school_r p:last-child span {
  display: inline-block;
  width: 0.3rem;
  text-align: center;
  line-height: 0.15rem;
  background: #28a289;
  color: #fff;
  font-size: 0.1rem;
}
.school_r p:last-child span:last-child {
  margin-left: 0.1rem;
}
.school_r p:last-child {
  display: flex;
  margin-bottom: 0.1rem;
}
</style>



