<template>
  <div class="consult">
    <div class="consult_detail">
      <ul>
        <li>
          <div class="consult_left">
            <canvas id="accepted" width="24" height="52">
                您的浏览器不支持canvas标签,请尽快升级
            </canvas>
            <p>已接受</p>
          </div>
          <div class="consult_right">
            <p>
              <span>发布时间:</span>
              <span>2019-04-26</span>
            </p>
            <p>
              <span>备注:</span>
              <span>兴趣测评结果问题的咨询</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {
    // 1,获取canvas
    var canvas = document.getElementById("accepted");
    // 2,获取2D渲染上下文
    var context = canvas.getContext("2d");
    // 3, 绘制矩形
    context.beginPath();
    context.moveTo(0, 0);
    context.lineTo(0, 52);
    context.lineTo(12, 42);
    context.lineTo(24, 52);
    context.lineTo(24, 0);
    context.closePath();
    context.fillStyle = "#f00";
    context.fill();
  }
};
</script>
<style scoped>
.consult_detail {
  width: 3.65rem;
  margin-left: 0.05rem;
}
.consult_detail li {
  width: 100%;
  background: #fff;
  display: flex;
}
.consult_left {
    position: relative;
}
.consult_left p {
    font-size: 0.13rem;
    color: #fff;
    position: absolute;
    top: -0.001rem;
    left: 0.05rem;
}
.consult_right {
    margin-top: 0.15rem;
    margin-left: 0.15rem;
    margin-bottom: 0.2rem;
}
.consult_right p:last-child {
    margin-top: 0.1rem;
}
</style>





