<template>
  <div class="myBookings">
    <div class="back_img"></div>
    <div class="book_box">
      <ul>
        <li v-for="(value, index) in mes" :key="index">
          <div>
            <img src="../../../static/img/peo.png" alt>
            <span>咨询师: {{value.name}}</span>
          </div>
          <div>
            <img src="../../../static/img/tecpic.png" alt>
            <span>预约时间: {{value.date}}</span>
            <p>
              {{value.times}}
            </p>
          </div>
          <div>
            <img src="../../../static/img/remark.png" alt>
            <span>备注: {{value.note}}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { studentOrder } from "@/request/api";
export default {
  data() {
    return {
      mes: null
    };
  },
  created() {
    this.getOrder();
  },
  methods: {
    getOrder() {
      studentOrder({
        id: 2
      }).then(res => {
        console.log(res.data);
        this.mes = res.data;
      });
    }
  }
};
</script>
<style scoped>
.myBookings {
  position: relative;
}
.back_img {
  width: 100%;
  height: 5.76rem;
  position: absolute;
  top: -0.5rem;
  background: url("../../../static/img/subpic.png") no-repeat;
}
.book_box {
  width: 2.9rem;
  height: 3.79rem;
  background: #fff;
  border-radius: 0.05rem;
  position: absolute;
  top: 1rem;
  left: 0.43rem;
}
.book_box div span {
  color: #333;
  font-size: 0.18rem;
}
.book_box div img {
  margin-right: 0.05rem;
}
.book_box div {
  margin-top: 0.23rem;
  margin-left: 0.28rem;
}
.book_box div:first-child {
  margin-top: 0.9rem;
  margin-left: 0.28rem;
}
.book_box div:nth-child(2) p {
  margin-top: 0.2rem;
  margin-left: 0.2rem;
}
.book_box div:nth-child(2) p span:first-child {
  color: aqua;
}
.book_box div:nth-child(2) p span:last-child {
  color: #45f;
}
</style>



