<template>
  <div class="refer">
    <div class="back_img"></div>
    <div class="counsel">
      <div class="cle_counsel">
        <div class="sma_cle">
           咨询
        </div>
      </div>
      <input class="contact_way" type="text" placeholder="请输入您的联系方式">
      <input class="your_name" type="text" placeholder="请输入姓名">
      <textarea class="your_mes" placeholder="请输入您的备注留言" cols="28" rows="2" style="color:#9c9c9c"></textarea>
    </div>
    <div class="sub" @click="saveMes()">提交</div>
    <Popup v-if="isShow" title="提交成功"></Popup>
  </div>
</template>
<script>
import Popup from "../common/Popup";
export default {
  name: "stu_msg",
  data() {
    return {
      isShow: false
    };
  },
  methods: {
    saveMes() {
      this.isShow = true;
      clearInterval(timer1);
      let timer1 = setTimeout(() => {
        //此时的this指向是该vue组件，不管在哪个地方使用，指向都是该vue组件。
        this.isShow = false;
      }, 1000);
    }
  },
  components: {
    Popup
  }
};
</script>
<style scoped>
/* 提交按钮的样式 */
.refer {
  position: relative;
}
.back_img {
  width: 100%;
  height: 2.53rem;
  position: absolute;
  top: -1rem;
  background: url("../../../static/img/headImg.png") no-repeat;
}
.sub {
  width: 2.58rem;
  height: 0.5rem;
  position: absolute;
  top: 3.5rem;
  left: 50%;
  margin-left: -1.29rem;
  background: #28A289;
  color: #fff;
  line-height: 0.5rem;
  text-align: center;
  border-radius: 0.05rem;
}
/* 输入框样式 */
.counsel {
  position: relative;
  top: 0.15rem;
  width: 2.9rem;
  height: 4.2rem;
  background: #fff;
  border-radius: 0.1rem;
  margin-left: 0.43rem;
  margin-top: 0.5rem;
  position: relative;
}
.cle_counsel {
  width: 0.53rem;
  height: 0.53rem;
  line-height: 0.53rem;
  text-align: center;
  border-radius: 50%;
  background: url("../../../static/img/bigRound.png") no-repeat;
  position: absolute;
  top: -0.12rem;
  left: 50%;
  margin-left: -0.265rem;
}
.sma_cle {
  width: 0.44rem;
  height: 0.44rem;
  color: #fff;
  line-height: 0.44rem;
  text-align: center;
  border-radius: 50%;
  background: url("../../../static/img/smaRound.png") no-repeat;
  margin: 0.05rem;
}
.contact_way,
.your_name {
  width: 2.6rem;
  line-height: 0.49rem;
  font-size: 0.18rem;
  border: 0.02rem solid #ffb84c;
  border-radius: 0.1rem;
}
.contact_way {
  margin-top: 0.73rem;
  margin-left: 0.15rem;
  color: #9c9c9c;
}
.your_name {
  margin-top: 0.15rem;
  margin-left: 0.15rem;
  color: #9c9c9c;
}
.your_mes {
  line-height: 0.49rem;
  font-size: 0.18rem;
  margin-top: 0.15rem;
  margin-left: 0.15rem;
  border: 0.02rem solid #ffb84c;
  border-radius: 0.1rem;
  outline: none;
  color: #9c9c9c;
}
</style>



