<template>
  <div class="popup">
    <div class="mask"></div>
    <div class="save_success">
      <img src="../../../static/img/save.png" alt>
      <p>{{title}}</p>
    </div>
  </div>
</template>
<script>
export default {
    // 公共的弹窗组件
    name: 'popup',
    data() {
        return {
            
        }
    },
    props: ['title']
}
</script>
<style scoped>
/* 蒙版样式 */
.mask {
    width: 100%;
    height: 7rem;
    background: #000;
    opacity: 0.8;
    position: fixed;
    top: 0;
    left: 0;
    bottom:0;
}
/* 保存成功弹窗样式 */
.save_success {
  width: 2.3rem;
  height: 1.55rem;
  background: #fff;
  border-radius: 0.1rem;
  position: absolute;
  top: 1.5rem;
  left: 0.74rem;
}
.save_success img {
  margin-top: 0.19rem;
  margin-left: 0.73rem;
}
.save_success p {
  margin-top: 0.21rem;
  text-align: center;
}
</style>




