<template>
    <div class="counselor">
        <div class="cou_tec">
            <ul>
                <li v-for="(value, index) in teachers" :key="index">
                    <div class="tec_l">
                        <img :src="value.headerPic" alt="">
                    </div>
                    <div class="tec_r">
                        <p>{{value.name}}</p>
                        <p>咨询师简介：{{value.intr}} <span class="iconfont">&#xe623;</span></p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { studentFoucs } from "@/request/api";
export default {
  data() {
    return {
      teachers: null
    };
  },
  created() {
    this.getMajor();
  },
  methods: {
    getMajor() {
      studentFoucs({
        id: 2
      }).then(res => {
        console.log(res.data);
        this.teachers = res.data.myCounselor;
        console.log(this.teachers);
      });
    }
  }
};
</script>
<style scoped>
.cou_tec {
    width: 100%;
    margin-top: 0.11rem;
}
.cou_tec li {
    width: 3.65rem;
    margin-left: 0.05rem;
    background: #fff;
    display: flex;
    justify-content: space-between;
    border-radius: 0.08rem;
    margin-bottom: 0.06rem;
}
.tec_l {
    width: 0.8rem;
    height: 0.8rem;
    margin-left: 0.2rem;
    margin: 0.1rem 0.2rem 0.1rem 0.2rem;
}
.tec_l img {
    width: 100%;
    height: 0.8rem;
}
.tec_r {
    width: 2.5rem;
    font-size: 0.12rem;
}

.tec_r p:first-child {
    margin-top: 0.18rem;
    font-size: 0.16rem;
}
.tec_r p:last-child {
    font-size: 0.14rem;
    margin-top: 0.3rem;
    position: relative;
}
.tec_r p:last-child .iconfont {
    font-size: 0.18rem;
    color: #FFBC3C;
    position: absolute;
    right: 0.1rem;
} 
</style>



