import Vue from 'vue'
import Router from 'vue-router'
import Footer from '@/components/common/Footer'
// 引入公共头组件
import Hea from '@/components/common/Hea'
// 引入我的组件和我的的子组件
import Mine from '@/components/Mine'
import myGrades from '@/components/mine/myGrades'
import addGrades from '@/components/mine/addGrades'
import myResult from '@/components/mine/myResult'
import myProfile from '@/components/mine/myProfile'
import myExam from '@/components/mine/myExam'
import testResult from '@/components/mine/testResult'
import visualTest from '@/components/mine/visualTest'
import Refer from '@/components/mine/Refer'
import myFocus from '@/components/mine/myFocus'
import Major from '@/components/mine/Major'
import Academy from '@/components/mine/Academy'
import Counselor from '@/components/mine/Counselor'
import myBookings from '@/components/mine/myBookings'
import followUs from '@/components/mine/followUs'
import Set from '@/components/mine/Set'
import Consult from '@/components/mine/Consult'
// 引入咨询和咨询的子组件
import Advisory from '@/components/Advisory'
import referTec from '@/components/advisory/referTec'
import tecDetail from '@/components/advisory/tecDetail'
// 引入查询和查询的子组件
import Query from '@/components/Query'
import Domain from '@/components/query/Domain'
import Specialty from '@/components/query/Specialty'
import Cademy from '@/components/query/Cademy'
import testScore from '@/components/query/testScore'
import City from '@/components/query/City'
import Advtec from '@/components/query/Advtec'
import advtecDetail from '@/components/query/advtecDetail'
import smartSearch from '@/components/query/smartSearch'
// 引入首页和首页的子组件
import Home from '@/components/Home'
import choSubject from '@/components/home/choSubject'
import exportsTest from '@/components/home/exportsTest'
import testTopic from '@/components/home/testTopic'
import selectTest from '@/components/home/selectTest'
import conform from '@/components/home/conform'
// 引入订购包页面
import Derive from '@/components/common/Derive'
Vue.use(Router)

export default new Router({
  routes: [
    // 配置页脚以及他的子路由
    {
      path: '/',
      redirect: '/home',
    },
    {
      path: '/footer',
      name: 'footer',
      component: Footer,
      children: [
      {
        path: '/home',
        name: 'home',
        components: {
          default: Home,
          header: Hea
        },
      },
      {
        path: '/home/choSubject',
        name: 'choSubject',
        components: {
          default: choSubject,
          header: Hea
        },
      },
      {
        path: '/home/exportsTest',
        name: 'exportsTest',
        components: {
          default: exportsTest,
          header: Hea
        },
      },
      {
        path: '/home/testTopic',
        name: 'testTopic',
        components: {
          default: testTopic,
          header: Hea
        },
      },
      {
        path: '/home/selectTest',
        name: 'selectTest',
        components: {
          default: selectTest,
          header: Hea
        },
      },
      {
        path: '/home/conform',
        name: 'conform',
        components: {
          default: conform,
          header: Hea
        },
      },
      {
        path: '/query',
        name: 'query',
        components: {
          default: Query,
          header: Hea
        },
      },
      {
        path: '/query/domain',
        name: 'domain',
        components: {
          default: Domain,
          header: Hea
        },
      },
      {
        path: '/query/specialty',
        name: 'specialty',
        components: {
          default: Specialty,
          header: Hea
        },
      },
      {
        path: '/query/cademy',
        name: 'cademy',
        components: {
          default: Cademy,
          header: Hea
        },
      },
      {
        path: '/query/testScore',
        name: 'testScore',
        components: {
          default: testScore,
          header: Hea
        },
      },
      {
        path: '/query/city',
        name: 'city',
        components: {
          default: City,
          header: Hea
        },
      },
      {
        path: '/query/advtec',
        name: 'advtec',
        components: {
          default: Advtec,
          header: Hea
        },
      },
      {
        path: '/query/advtecdetail',
        name: 'advtecdetail',
        components: {
          default: advtecDetail,
          header: Hea
        },
      },
      {
        path: '/query/smartSearch',
        name: 'smartSearch',
        components: {
          default: smartSearch,
          header: Hea
        },
      },
      {
        path: '/advisory',
        name: 'advisory',
        components: {
          default: Advisory,
          header: Hea
        },
      },
      {
        path: '/mine',
        name: 'mine',
        components: {
          default: Mine,
          header: Hea
        },
      }
    ]
    },
    {
      path: '/advisory/referTec',
      name: 'referTec',
      components: {
        default: referTec,
        header: Hea
      },
    },
    {
      path: '/advisory/tecDetail',
      name: 'tecDetail',
      components: {
        default: tecDetail,
        header: Hea
      },
    },
    {
      path: '/mine/myGrades',
      name: 'myGrades',
      components: {
        default: myGrades,
        header: Hea
      },
    },
    {
      path: '/mine/derive',
      name: 'derive',
      components: {
        default: Derive,
        header: Hea
      },
    },
    {
      path: '/mine/addGrades',
      name: 'addGrades',
      components: {
        default: addGrades,
        header: Hea
      },
    },
    {
      path: '/mine/myResult',
      name: 'myResult',
      components: {
        default: myResult,
        header: Hea
      },
    },
    {
      path: '/mine/myProfile',
      name: 'myProfile',
      components: {
        default: myProfile,
        header: Hea
      },
    },
    {
      path: '/mine/myExam',
      name: 'myExam',
      components: {
        default: myExam,
        header: Hea
      },
    },
    {
      path: '/mine/testResult',
      name: 'testResult',
      components: {
        default: testResult,
        header: Hea
      },
    },
    {
      path: '/mine/visualTest',
      name: 'visualTest',
      components: {
        default: visualTest,
        header: Hea
      },
    },
    {
      path: '/mine/myBookings',
      name: 'myBookings',
      components: {
        default: myBookings,
        header: Hea
      },
    },
    {
      path: '/mine/consult',
      name: 'consult',
      components: {
        default: Consult,
        header: Hea
      },
    },
    {
      path: '/mine/followUs',
      name: 'followUs',
      components: {
        default: followUs,
        header: Hea
      },
    },
    {
      path: '/mine/Refer',
      name: 'refer',
      components: {
        default: Refer,
        header: Hea
      },
    },
    {
      path: '/mine/Set',
      name: 'Set',
      components: {
        default: Set,
        header: Hea
      },
    },
    {
      path: '/mine/myFocus',
      name: 'myFocus',
      component: myFocus,
      children:[
        {
          path: '/mine/myFocus',
          redirect: '/mine/myFocus/major',

        },
        {
          path: '/mine/myFocus/major',
          name: 'major',
          component: Major
        },
        {
          path: '/mine/myFocus/academy',
          name: 'academy',
          component: Academy
        },
        {
          path: '/mine/myFocus/counselor',
          name: 'counselor',
          component: Counselor
        }
      ]
    },
  ]
})



