
<template>
        <Header class="hea">
            <span @click="jump_back()" class="iconfont">&#xe614;</span>
        </Header>
</template>
<script>
// 封装的公共头部组件
export default{
        name: 'hea',
        data() {
            return {
                
            }
        },
        methods: {
            jump_back() {
                this.$router.go(-1);
            }
        },
    }
</script>
<style scoped>
    .hea {
        width: 100%;
        height: 0.5rem;
        position: relative;
        z-index: 1;
    }
    .hea span {
        line-height: 0.5rem;
        margin-left: 0.15rem;
        color: #B2B2B2;
    }
</style>



