<template>
    <div class="myExam">
        <div class="subject">
            <ul>
                <li>
                    <span>政治</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>历史</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>地理</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>技术</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>物理</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>化学</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>生物</span>
                    <i class="el-icon-check"></i>
                </li>
                <li>
                    <span>语文</span>
                    <i class="el-icon-check"></i>
                </li>
            </ul>
        </div>
        <div class="btn">
            <el-button type="primary" plain class="done_btn">完成</el-button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'myExam',
    data() {
        return {
            isYellow: false 
        }
    },
}
</script>
<style scoped>
.subject li .nor {
    width: 0.19rem;
    height: 0.19rem;
    border: 0.01rem solid #B2B2B2;
    border-radius: 50%;
}
.subject li .yel {
    width: 0.19rem;
    height: 0.19rem;
    border-radius: 50%;
    background: #FFB84C;
    color: #fff;
}
/* 完成按钮样式 */
.btn {
    margin-top: 0.62rem;
    display: flex;
    justify-content: center;
}
.done_btn {
    width: 2.58rem;
    height: 0.5rem;
    background: #389AFF;
    color: #fff;
}
.subject {
    width: 100%;
    overflow: hidden;
    background: #fff;
}
.subject li {
    width: 100%;
    height: 0.5rem;
    border-bottom: 0.01rem solid #F3F3F3;
    line-height: 0.5rem;
}
.subject li span {
    margin-left: 0.15rem;
    margin-right: 2.96rem;
    color: #333333;
}
.subject li i {
    font-size: 0.2rem;
    color: #B2B2B2;
}
</style>





