<template>
  <div class="tecDetail">
    <div class="bac_img"></div>
    <div class="coun_details">
      <div class="tec_pic"></div>
      <div class="tec_mes">
        <div class="mes_l">
          <p>叶龙飞</p>
          <el-rate class="star" v-model="value1"></el-rate>
          <p>18812341234</p>
        </div>
        <div class="mes_r">
          <div class="att">+关注</div>
          <div class="icon_phone">
            <i class="el-icon-phone"></i>
          </div>
        </div>
      </div>
      <div class="tec_work">
        <p>专业的心理咨询师的工作主要包括以下内容：从来访者及家属等信息源获得有关来访者的心理问题、心理障碍的资料；对来访者的心理成长、人格发展、智力、社会化及家庭、婚姻生活事件等进行全面评估。</p>
      </div>
    </div>
    <div class="block">
      <span class="demonstration">默认</span>
      <el-date-picker v-model="value1" type="date" placeholder="选择日期"></el-date-picker>
    </div>
    <div class="ready_time">
      <ul>
        <li>9:00-10:00</li>
        <li>10:30-11:30</li>
        <li>13:30-14:30</li>
        <li>15:00-16:00</li>
        <li>18:30-19:30</li>
        <li>20:00-21:00</li>
      </ul>
    </div>
    <div class="order_btn">
      <el-button type="success" @click="saveMes()">立即预约</el-button>
    </div>
    <Popup v-if="isShow" title="预约成功"></Popup>
  </div>
</template>
<script>
import Calendar from "../common/Calendar";
import Popup from "../common/Popup";
export default {
  data() {
    return {
      value1: 5,
      isShow: false
    };
  },
  methods: {
    saveMes() {
      this.isShow = true;
      clearInterval(timer1);
      let timer1 = setTimeout(() => {
        //此时的this指向是该vue组件，不管在哪个地方使用，指向都是该vue组件。
        this.isShow = false;
      }, 1000);
    }
  },
  components: {
    Calendar,
    Popup
  }
};
</script>
<style scoped>
/* 蒙版样式 */
.mask {
  width: 100%;
  height: 7rem;
  background: #000;
  opacity: 0.8;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
}
/* 保存成功弹窗样式 */
.save_success {
  width: 2.3rem;
  height: 1.55rem;
  background: #fff;
  border-radius: 0.1rem;
  position: absolute;
  top: 3rem;
  left: 0.74rem;
  opacity: 1 !important;
}
.save_success img {
  margin-top: 0.19rem;
  margin-left: 0.73rem;
}
.save_success p {
  margin-top: 0.21rem;
  text-align: center;
}
/* 按钮样式 */
.order_btn {
  margin-left: 0.58rem;
}
.order_btn button {
  width: 2.58rem;
  height: 0.5rem;
  background: #28a289;
}
.ready_time {
  width: 2.34rem;
  position: absolute;
  top: 3.2rem;
  left: 0.72rem;
  margin-top: 0.2rem;
}
.ready_time ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.ready_time li {
  width: 0.69rem;
  height: 0.18rem;
  border-radius: 0.03rem;
  background: #ececec;
  font-size: 0.12rem;
  margin-bottom: 0.1rem;
  text-align: center;
  line-height: 0.18rem;
}
.tecDetail {
  position: relative;
}
.cal {
  width: 2.31rem;
  position: absolute;
  left: 0.72rem;
  right: 0;
  top: 1.7rem;
}
.tec_work {
  width: 2.61rem;
  height: 0.85rem;
  margin: 0 0.19rem 0.16rem 0.21rem;
  border-bottom: 0.05rem dotted #ececec;
}
.tec_work p {
  text-indent: 0.2rem;
  font-size: 0.13rem;
  color: #033333;
}
.icon_phone {
  margin-top: 0.18rem;
  margin-left: 0.14rem;
}
.icon_phone i {
  color: #ffb84c;
}
.att {
  width: 0.4rem;
  height: 0.2rem;
  border: 0.02rem solid #ffb84c;
  font-size: 0.12rem;
  color: #ffb84c;
  line-height: 0.2rem;
  margin-top: 0.1rem;
}
.mes_l p:first-child {
  padding-top: 0.05rem;
  font-size: 0.16rem;
}
.mes_l p:last-child {
  font-size: 0.15rem;
  margin-top: 0.05rem;
}
.star {
  font-size: 0.12rem;
  transform: scale(0.9);
  margin-left: -0.08rem;
  margin-top: 0.08rem;
}
.tec_mes {
  width: 1.85rem;
  height: 0.57rem;
  margin: 0.9rem 0.8rem 0.24rem 1.1rem;
  display: flex;
  justify-content: space-between;
}
.bac_img {
  width: 100%;
  height: 2.53rem;
  background: url("../../../static/img/a1.png") no-repeat;
  position: absolute;
  left: 0;
  right: 0;
  top: -1.4rem;
}
.coun_details {
  width: 3rem;
  height: 4.2rem;
  background: #fff;
  border-radius: 0.05rem;
  position: relative;
  margin: 0.35rem 0.38rem;
}
.tec_pic {
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 0.05rem;
  background: lightpink;
  position: absolute;
  left: 0.12rem;
  top: -0.17rem;
}
</style>



