<template>
  <div class="meno">
    <!-- <button  @click="getPrveMonth">上个月</button>
    <br>
    <button  @click="getNextMonth">下个月</button>-->
    <div class="arrows">
      <i @click="getPrveMonth" class="el-icon-arrow-left"></i>
      <div>{{currentYear}}年{{(currentMonth)}}月</div>
      <i @click="getNextMonth" class="el-icon-arrow-right"></i>
    </div>
    <!-- 日历组件 -->
    <div class="content">
      <Xsmcalendar ref="smcalendar" :data="calendarData" @itemClick="itemClick"></Xsmcalendar>
    </div>
  </div>
</template>
 
<script>
// 导入XSmcalendar日历
import Xsmcalendar from './Xsmcalendar';
// 封装的日历组件
export default {
  // 组件声明
  components: {
    // 声明
    Xsmcalendar
  },
  // 方法
  methods: {
    //
    initDate() {
      // 获取日历组件当前时间
      this.currentYear = this.smcalendar.getSMCalendarYear();
      this.currentMonth = this.smcalendar.getSMCalendarMonth() + 1;
    },
    // 日历点击回调
    itemClick(data) {
      console.log("点击日历回传的值：");
      console.log(data);
    },
    // 上个月
    getPrveMonth() {
      this.smcalendar.getPrveMonth(
        this.smcalendar.getSMCalendarYear(),
        this.smcalendar.getSMCalendarMonth()
      );
      this.smcalendar.reload();
      this.initDate();
    },
    // 下个月
    getNextMonth() {
      this.smcalendar.getNextMonth(
        this.smcalendar.getSMCalendarYear(),
        this.smcalendar.getSMCalendarMonth()
      );
      this.smcalendar.reload();
      this.initDate();
    }
  },
  //
  mounted() {
    //
    this.smcalendar = this.$refs.smcalendar;
    //
    this.initDate();
  },
  data() {
    return {
      currentYear: null,
      currentMonth: null,
      smcalendar: null,
      // 模拟数据请求
      calendarData: [
        {
          ATTEND_TYPE: "1",
          ATTEND_CURR_DATE: "2018-10-18",
          ATTEND_CLOCKIN_CNT: "2",
          WORKING_HOURS: "40"
        },
        {
          ATTEND_TYPE: "2",
          ATTEND_CURR_DATE: "2018-10-19",
          ATTEND_CLOCKIN_CNT: "1",
          WORKING_HOURS: "60"
        },
        {
          ATTEND_TYPE: "2",
          isDay: "1",
          ATTEND_CURR_DATE: "2018-10-28",
          ATTEND_CLOCKIN_CNT: "1",
          WORKING_HOURS: "60"
        },
        {
          ATTEND_TYPE: "0",
          isDay: "0",
          ATTEND_CURR_DATE: "2018-11-28",
          ATTEND_CLOCKIN_CNT: "4",
          WORKING_HOURS: "50"
        }
      ]
    };
  }
};
</script>
 
<style>
.meno {
  font-family: "PingFang SC", "\82F9\65B9", "PingFangSC-Regular",
    "Helvetica Neue", Helvetica, STHeiTi, sans-serif;
  -webkit-user-select: none;
  user-select: none;
  color: #676767;
  /* background-color: #f6f6f6; */
  font-size: 0.14rem;
}
.arrows {
  width: 100%;
  height: 0.3rem;
  display: flex;
  justify-content: space-between;
  line-height: 0.3rem;
}
.arrows i {
  font-size: 0.2rem;
  line-height: 0.3rem;
}
</style>



