<template>
  <div class="visualTest">
    <h3>
      <img class="sma" src="../../../static/img/smaL.png" alt>
      <img class="mid" src="../../../static/img/midL.png" alt>
      <img class="big" src="../../../static/img/bigL.png" alt>
      测评结果
      <img class="bigR" src="../../../static/img/bigL.png" alt>
      <img class="midR" src="../../../static/img/midL.png" alt>
      <img class="smaR" src="../../../static/img/smaL.png" alt>
    </h3>
    <div class="interest">
      <p>兴趣测评结果：社会型</p>
      <p>你是一个很看重人际关系的人，喜欢与人打交道，重视人群的和谐程度，关注别人和自己的感受，并精于言辞，行为举止得体，特别善于交际，因此交友广泛</p>
    </div>
    <div id="myChart" :style="{width: '350px', height: '350px'}"></div>
    <div class="two_btn">
      <router-link to="/mine/derive">
        <div class="educe">导出</div>
      </router-link>
      <router-link to="/mine/Refer">
        <div class="consult">咨询</div>
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  name: "hello",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: "axis"
        },
        legend: {
          x: "center"
        },
        toolbox: {
          show: true
        },
        calculable: true,
        polar: [
          {
            indicator: [
              { text: "企业型", max: 100 },
              { text: "研究型", max: 100 },
              { text: "常规型", max: 100 },
              { text: "艺术型", max: 100 },
              { text: "现实型", max: 100 },
              { text: "社会型", max: 100 }
            ],
            radius: 100
          }
        ],
        series: [
          {
            type: "radar",
            itemStyle: {
              normal: {
                areaStyle: {
                  type: "default"
                }
              }
            },
            data: [
              {
                value: [97, 42, 88, 94, 90, 86]
              },
              {
                value: [97, 32, 74, 95, 88, 92]
              }
            ]
          }
        ]
      });
    }
  }
};
</script>
<style scoped>
.visualTest {
  margin-top: 0.3rem;
  position: relative;
}
.sma {
  width: 0.05rem;
  height: 0.05rem;
  position: absolute;
  top: 0.09rem;
  left: 1.27rem;
}
.mid {
  width: 0.08rem;
  height: 0.08rem;
  position: absolute;
  top: 0.07rem;
  left: 1.3rem;
}
.big {
  width: 0.1rem;
  height: 0.1rem;
}
.bigR {
  width: 0.1rem;
  height: 0.1rem;
}
.midR {
  width: 0.08rem;
  height: 0.08rem;
  position: absolute;
  top: 0.07rem;
  right: 1.3rem;
}
.smaR {
  width: 0.05rem;
  height: 0.05rem;
  position: absolute;
  top: 0.09rem;
  right: 1.26rem;
}
.educe {
  width: 0.98rem;
  height: 0.5rem;
  color: #28a289;
  font-size: 0.18rem;
  border: 0.015rem solid #28a289;
  border-radius: 0.03rem;
  text-align: center;
  line-height: 0.5rem;
}
.consult {
  width: 0.98rem;
  height: 0.5rem;
  font-size: 0.18rem;
  line-height: 0.5rem;
  text-align: center;
  background: #28a289;
  color: #fff;
  border-radius: 0.03rem;
}
.two_btn {
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: space-around;
}
/* 可视化数据样式 */
#myChart {
  margin-top: 0.48rem;
  margin-left: 0.1rem;
}
/* 标题样式 */
.visualTest h3 {
  text-align: center;
  color: #28a289;
}
.interest {
  margin-top: 0.27rem;
  width: 3.15rem;
  height: 0.69rem;
  margin-left: 0.28rem;
}
.interest p {
  margin-bottom: 0.07rem;
  color: #333333;
}
</style>



