<template>
  <div class="advtec">
    <div class="title_banner">咨询师</div>
    <button class="cho_btn" readonly="readonly" @click="showAddressPicker()">
      <span>选择城市</span>
      <i class="el-icon-caret-bottom"></i>
    </button>
    <div class="cou_tec">
      <ul>
        <router-link to="/query/advtecdetail">
          <li>
            <div class="tec_l"></div>
            <div class="tec_r">
              <p>叶龙飞</p>
              <p>
                <span>BCf</span>
                <span>NLP</span>
              </p>
              <p>
                咨询师简介：国家二级心理咨询师
                <span class="iconfont">&#xe623;</span>
              </p>
            </div>
          </li>
        </router-link>
        <li>
          <div class="tec_l"></div>
          <div class="tec_r">
            <p>叶龙飞</p>
            <p>
              <span>BCf</span>
              <span>NLP</span>
            </p>
            <p>
              咨询师简介：国家二级心理咨询师
              <span class="iconfont">&#xe623;</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <levelThree ref="son"></levelThree>
  </div>
</template>
<script>
import levelThree from "../common/levelThree";
export default {
  components: {
    levelThree
  },
  components: {
    levelThree
  },
  methods: {
    showAddressPicker() {
      this.$refs.son.showAddressPicker();
    }
  }
};
</script>
<style scoped>
/* 选择城市按钮样式 */
.cho_btn {
  width: 0.8rem;
  height: 0.24rem;
  background: #ffb84c;
  border: 0;
  display: flex;
  justify-content: space-around;
  border-radius: 0.03rem;
  outline: none;
  position: absolute;
  right: 0.1rem;
  top: 0.43rem;
}
.cho_btn span,
i {
  font-size: 0.12rem;
  color: #fff;
  line-height: 0.24rem;
}
/*  */
.advtec {
  position: relative;
}
.title_banner {
  width: 100%;
  height: 0.4rem;
  background: #28a289;
  font-size: 0.18rem;
  color: #fff;
  line-height: 0.4rem;
  text-align: center;
}
/* 咨询师列表样式 */
.cou_tec {
  width: 100%;
  margin-top: 0.29rem;
}
.cou_tec li {
  width: 3.65rem;
  margin-left: 0.05rem;
  background: #fff;
  display: flex;
  justify-content: space-between;
  border-radius: 0.08rem;
  margin-bottom: 0.06rem;
}
.tec_l {
  width: 0.8rem;
  height: 0.8rem;
  margin-left: 0.2rem;
  margin: 0.1rem 0.2rem 0.1rem 0.2rem;
  background: antiquewhite;
}
.tec_r {
  width: 2.5rem;
  font-size: 0.12rem;
}
.tec_r p {
  color: #333;
}
.tec_r p:first-child {
  margin-top: 0.18rem;
  font-size: 0.16rem;
}
.tec_r p:nth-child(2) {
  margin-top: 0.11rem;
  margin-bottom: 0.09rem;
}
.tec_r p:nth-child(2) span {
  display: inline-block;
  width: 0.3rem;
  height: 0.15rem;
  line-height: 0.15rem;
  text-align: center;
  border-radius: 0.03rem;
  background: #28a289;
  margin-right: 0.14rem;
  color: #fff;
}
.tec_r p:last-child .iconfont {
  font-size: 0.18rem;
  margin-left: 0.35rem;
  color: #ffbc3c;
  vertical-align: -0.05rem;
}
</style>



