<template>
  <div class="major">
    <div class="career">
      <ul>
        <router-link to="/query/specialty">
          <li v-for="(value, index) in majors" :key="index">
            <i class="iconfont">&#xe60c;</i>
            <span>专业名称: {{value.majorName}}</span>
          </li>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
import { studentFoucs } from "@/request/api";
export default {
  data() {
    return {
      majors: null
    };
  },
  created() {
    this.getMajor();
  },
  methods: {
    getMajor() {
      studentFoucs({
        id: 2
      }).then(res => {
        console.log(res.data);
        this.majors = res.data.myMajor;
        console.log(this.majors);
      });
    }
  }
};
</script>
<style scoped>
/* 专业样式 */
.career {
  width: 100%;
  margin-top: 0.11rem;
  display: flex;
  justify-content: center;
}
.career li {
  width: 3.65rem;
  height: 0.7rem;
  background: #fff;
  margin-bottom: 0.07rem;
  border-radius: 0.05rem;
  color: #333;
}
.career li  i {
  /* margin-left: -0.2rem; */
  line-height: 0.7rem;
  color: #ffbc3c;
  margin-left: 0.3rem;
}
</style>




