<template>
  <div class="myResult">
    <div class="total_points">
      <div class="total_show">
        <p>成绩</p>
        <p>总分:640</p>
      </div>
    </div>
    <div class="classify_result">
      <ul>
        <li v-for="(value, index) in studentGrades" :key="index">
          <span>{{value.subject}}</span>
          <span>{{value.score}}</span>
        </li>
      </ul>
    </div>
    <div class="hint">
      <span class="iconfont warn">&#xe8eb;</span>
      <div class="warm_prompt">
        <p>温馨提示:我的成绩不可更改</p>
        <p>谨慎填写哦!</p>
      </div>
    </div>
  </div>
</template>
<script>
import { testMsg } from "@/request/api";
export default {
  data() {
    return {
        studentGrades: null
    };
  },
  created() {
      this.getMsg();
  },
  methods: {
    getMsg() {
      testMsg({
          id: 2
      }).then(res => {
        // 获取数据成功后的其他操作
        console.log(res.data);
        this.studentGrades = JSON.parse(res.data.collegeEntranceExamGrade);
        console.log(this.studentGrades);
      });
    }
  }
};
</script>
<style scoped>
/* 温馨提示样式 */
.warn {
  font-size: 0.18rem;
  color: #efca1e;
  margin-right: 0.1rem;
  margin-top: -0.03rem;
}
.hint {
  display: flex;
  justify-content: center;
  margin-top: 0.52rem;
}
.warm_prompt p {
  text-align: center;
  color: #9c9c9c;
}
/* 每一科分数样式 */
.classify_result {
  width: 100%;
  margin-top: 0.05rem;
  overflow: hidden;
  background: #fff;
}
.classify_result li {
  width: 90%;
  height: 0.54rem;
  line-height: 0.54rem;
  border-bottom: 0.01rem solid #f3f3f3;
  margin-left: 5%;
}
.classify_result span {
  margin-left: 0.77rem;
}
/* 总分样式 */
.total_points {
  width: 100%;
  height: 0.95rem;
  background: #fff;
  display: flex;
  justify-content: center;
  align-content: center;
}
.total_show {
  width: 3.45rem;
  height: 0.65rem;
  margin-top: 0.15rem;
  border-radius: 0.05rem;
  background: url("../../../static/img/gross.png") no-repeat;
  background-size: 100% 100%;
}
.total_show p {
  color: #fff;
  text-align: center;
  margin-top: 0.13rem;
}
</style>



