<template>
  <div class="myFocus">
    <div class="head_page">
      <div class="title_head">
        <span @click="jump_back()" class="iconfont">&#xe614;</span>
      </div>
      <ul>
        <router-link to="/mine/myFocus/major">
          <li>
            <span class="word">专业</span>
          </li>
        </router-link>
        <router-link to="/mine/myFocus/academy">
          <li>
            <span class="word">院校</span>
          </li>
        </router-link>
        <router-link to="/mine/myFocus/counselor">
          <li>
            <span class="word">咨询师</span>
          </li>
        </router-link>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import {studentFoucs} from '@/request/api'
export default {
  data() {
    return {
      
    };
  },
  created() {
    this.getMajor();
  },
  methods: {
    jump_back() {
      this.$router.push({ name: "mine"});
    },
    getMajor() {
      studentFoucs({
        id: 2
      }).then(res => {
        console.log(res);
      })
    }
  }
};
</script>
<style scoped>
.router-link-active span {
  border-bottom: 0.03rem solid #ffb84c;
}
.head_page ul {
  width: 100%;
  height: 0.33rem;
  line-height: 0.33rem;
  display: flex;
  justify-content: space-around;
}
.head_page ul li {
  color: #333;
}
/* 回来 */
.title_head {
  width: 100%;
  height: 0.5rem;
  position: relative;
  z-index: 1;
}
.title_head span {
  line-height: 0.5rem;
  margin-left: 0.15rem;
  color: #b2b2b2;
}
</style>




